import type { KcContextBase, KcProps } from 'keycloakify'
import { clsx } from 'keycloakify/lib/tools/clsx'
import { memo } from 'react'
import type { I18n } from '../../i18n'
import type { TemplateProps } from '../template'
import DefaultTemplate from '../template'

export type LoginResetPasswordProps = KcProps & {
  kcContext: KcContextBase.LoginResetPassword
  i18n: I18n
  doFetchDefaultThemeResources?: boolean
  Template?: (props: TemplateProps) => JSX.Element | null
}

const LoginResetPassword = memo((props: LoginResetPasswordProps) => {
  const {
    kcContext,
    i18n,
    doFetchDefaultThemeResources = true,
    Template = DefaultTemplate,
    ...kcProps
  } = props

  const { url, realm, auth } = kcContext

  const { msg, msgStr } = i18n

  return (
    <Template
      {...{ kcContext, i18n, doFetchDefaultThemeResources, ...kcProps }}
      displayMessage={false}
      headerNode={
        <>
          <h1 style={{ color: 'black', fontWeight: 700, fontSize: '2rem' }}>
            {msg('emailForgotTitle')}
          </h1>
        </>
      }
      formNode={
        <form
          id='kc-reset-password-form'
          className={clsx(kcProps.kcFormClass)}
          action={url.loginAction}
          method='post'
        >
          <div className={clsx(kcProps.kcFormGroupClass)}>
            <div className={clsx(kcProps.kcLabelWrapperClass)}>
              <label htmlFor='username' className={clsx(kcProps.kcLabelClass)}>
                {!realm.loginWithEmailAllowed
                  ? msg('username')
                  : !realm.registrationEmailAsUsername
                  ? msg('usernameOrEmail')
                  : msg('email')}
              </label>
            </div>
            <div className={clsx(kcProps.kcInputWrapperClass)}>
              <input
                type='text'
                id='username'
                name='username'
                className={clsx(kcProps.kcInputClass)}
                autoFocus
                defaultValue={
                  auth !== undefined && auth.showUsername ? auth.attemptedUsername : undefined
                }
              />
            </div>
          </div>
          <div className={clsx(kcProps.kcFormGroupClass, kcProps.kcFormSettingClass)}>
            <div id='kc-form-buttons' className={clsx(kcProps.kcFormButtonsClass)}>
              <input
                className={clsx(
                  kcProps.kcButtonClass,
                  kcProps.kcButtonPrimaryClass,
                  kcProps.kcButtonBlockClass,
                  kcProps.kcButtonLargeClass
                )}
                type='submit'
                value={msgStr('doSubmit')}
              />
              <div
                style={{
                  width: '100%',
                  height: '1px',
                  background: 'rgba(0, 0, 0, 0.1)',
                  margin: '2rem 0 1.5rem 0',
                }}
              />
              <div id='kc-form-options' className={clsx(kcProps.kcFormOptionsClass)}>
                <div className={clsx(kcProps.kcFormOptionsWrapperClass)}>
                  <span
                    style={{
                      display: 'flex',
                      color: '#333333',
                      fontSize: '1rem',
                      fontWeight: 500,
                    }}
                  >
                    JÃ¡ possui uma conta? {'  '} <a href={url.loginUrl}> {msg('backToLogin')}</a>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </form>
      }
      infoNode={msg('emailInstruction')}
    />
  )
})

export default LoginResetPassword
